/* eslint-disable max-len */

import {
  createAuthenticatedUser,
  createComment,
  createUser,
  ReactionType,
  setAuthenticatedUser,
  setComment,
} from '@zetecom/app-core';

import { Demo } from '~/demos';

import { Comment } from './Comment';

const arthur = createUser({ id: '2', nick: 'Arthur' });
const perceval = createAuthenticatedUser({ id: '3', nick: 'Seigneur Perceval' });
const loth = createUser({ id: '4', nick: 'Le Roi Loth' });
const lodagan = createUser({ id: '5', nick: 'Lodagan' });

const connard = createComment({
  id: '10',
  author: arthur,
  text: 'Mais vous tes pas mort, espce de connard ?',
});

const chevres = createComment({
  id: '9',
  author: lodagan,
  text: "Moi une fois, j'tais tellement raide que j'avais l'impression de me faire attaquer de tous cts, j'me dfendais, j'me dfendais... En fait, j'tais dans un pturage, j'ai tu 76 chvres !",
});

const approche = createComment({
  id: '8',
  author: perceval,
  text: "Une fois,  une excution, je m'approche d'une fille. Pour rigoler, je lui fais :  Vous tes de la famille du pendu ? ... C'tait sa sur. Bonjour l'approche !",
});

const faux = createComment({
  id: '7',
  author: perceval,
  text: 'Cest pas faux.',
});

const merdique = createComment({
  id: '6',
  author: loth,
  text: 'Parce que votre existence est merdique, mon pauvre ami... Vous avez lil qui brille  chaque fois quun oiseau pte ! Cest triste  voir ! a fait des annes que vous menez un train de vie de noix de St-Jacques, alors videmment, un message... qui annonce la visite dun imbcile, porteur de bonnes nouvelles, cest dj un ptit festival pour vous ! Jsuis sr que vous vous tes peign pour loccasion !',
});

const crame = createComment({
  id: '5',
  author: arthur,
  text: 'Ah, le printemps ! La nature se rveille, les oiseaux reviennent, on crame des mecs.',
  replies: [approche],
});

const lire = createComment({
  id: '4',
  author: lodagan,
  text: 'Moi jai appris  lire, ben je souhaite a  personne.',
});

const vieilleBranch = createComment({
  id: '3',
  author: loth,
  text: 'Ah !  ce propos, je dois vous avouer que j\'ai longtemps rflchi  savoir comment vous deviez m\'appeler. [...] h oui ! Parce que vous tes pas,  proprement parler, un de mes sujets. Vous tes pas sous mes ordres non plus. "Sire" c\'est quand mme un peu ampoul. Alors j\'ai pens  beaucoup de choses. "Loth", "Seigneur", "Monsieur"... Je me suis mme demand si vous deviez pas m\'appeler "vieille branche", "mon p\'tit pote" ou "canaillou".',
  replies: [chevres, faux],
});

const tourne = createComment({
  id: '2',
  author: perceval,
  text: 'Faut arrter ces conneries de nord et de sud ! Une fois pour toutes, le nord, suivant comment on est tourn, a change tout !',
  subscribed: true,
  replies: [connard, crame, merdique],
});

const druide = createComment({
  id: '1',
  author: arthur,
  text: "Au bout d'un moment, il est vraiment druide, c'mec-l, ou a fait quinze ans qu'il me prend pour un con ?",
  reactionsCount: {
    like: 6,
    approve: 10,
    think: 3,
    disagree: 0,
    dontUnderstand: 1,
  },
  userReaction: ReactionType.approve,
  replies: [tourne, vieilleBranch],
});

const comments = [druide, tourne, vieilleBranch, lire, crame, merdique, faux, approche, chevres, connard];
const rootComment = comments[0];

export const asRandomUser: Demo = {
  description: 'Seen as an unauthenticated user',
  prepare: ({ store, containers }) => {
    store.dispatch(setComment({ ...rootComment, replies: [] }));
    containers.comments.addItems(comments);
  },
  render: () => <Comment commentId={rootComment.id} />,
};

export const asLoggedInUser: Demo = {
  description: `Seen as a logged in user (${perceval.nick})`,
  prepare: ({ store, containers }) => {
    store.dispatch(setComment({ ...rootComment, replies: [] }));
    store.dispatch(setAuthenticatedUser(perceval));
    containers.comments.addItems(comments);
  },
  render: () => <Comment commentId={rootComment.id} />,
};
